import {
  useStudentStatsClassListQuery,
  useStudentStatsOneClassStudentListQuery,
  useStudentStatsLectureWiseStudentListQuery,
  useStudentStatsLectureWiseOneStudentSubjectListQuery,
  useStudentProfileAttendanceStatsDayWiseQuery,
  useStudentProfileAttendanceStatsLectureWiseQuery,
  useStudentDateWiseTimetableQuery,
  useStudentMcqExamStartTestMutation,
  useStudentMcqExamTestsetDetailQuery,
  useStudentMcqExamQuestionSaveMutation,
  useStudentMcqExamSubmitMutation,
  useStudentMcqExamResultDetailQuery,
  useStudentMcqExamResultValidationMutation,
} from "../../../Services/StudentManager/studentApiDynamic";

export const useStudentStatsClassList = (args) => {
  const { data, refetch, isFetching } = useStudentStatsClassListQuery(
    args.data,
    {
      skip: args.skip,
    }
  );
  return {
    studentStatsClassList: data,
    studentStatsClassListRefetch: refetch,
    studentStatsClassListLoading: isFetching,
  };
};

