// for student mcq related things

export const studentMcqExamStartTestFunction = (builder) => {
  return builder.mutation({
    query: (data) => ({
      url: `/v1/mcq/start/by/student/testset/${data?.stid}/exam/query`,
      method: PATCH,
      body: data.bodyParameter,
    }),
  });
};

